nav {
  background: rgba(28, 235, 1, 0.151);
  backdrop-filter: blur(10px);
  position: fixed;
  display: flex;
  align-items: center;
  top: 0px;
  right: 0px;
}

nav button {
  border: transparent;
  background: transparent;
}

nav button:hover {
  transition: var(--transition);
  color: var(--border-clr-alter);
}

.act {
  font-size: 1rem;
  color: var(--border-clr);
}

@media screen and (max-width: 600px) {
  nav {
    display: grid;
    top: 0;
  }
}
